import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DevelopersComponent } from './developers.component';
import { TranslateModule } from '@ngx-translate/core';
import { RouterModule, Routes } from '@angular/router'; 
import { NgZorroAntModule } from '../../ng-zorro-ant.module';
import { PolicyComponent } from './policy/policy.component';
const routes: Routes = [
  {
    path: 'support',
    loadChildren: ()=> import('./support/support.module').then(m=>m.SupportModule)
  },
  {
    path: 'online-payment',
    loadChildren: ()=> import('./online-payment/online-payment.module').then(m=>m.OnlinePaymentModule)
  },
  {
    path: 'history-payment',
    loadChildren: ()=> import('./history-payment/history-payment.module').then(m=>m.HistoryPaymentModule)
  },
  {
    path: 'policy',
    component: PolicyComponent
  },
  {
    path: 'manage-key',
    loadChildren: ()=> import('./manage-keys/manage-keys.module').then(m=>m.ManageKeysModule)
  },
]

@NgModule({
  declarations: [
    DevelopersComponent,
    PolicyComponent
  ],
  imports: [
    CommonModule,
    TranslateModule,
    NgZorroAntModule,
    RouterModule.forChild(routes)
  ]
})
export class DevelopersModule { }
